"use client";
import React, { useState, useCallback, useRef, useEffect, useMemo } from 'react';
import debounce from 'lodash.debounce';

// Components
import { Sidebar } from './Sidebar';
import { Editor } from './Editor';
import BottomSheet from './BottomSheet';
import { UserDropdown } from './ui/UserDropdown';
import { Button } from "./ui/button";
import { useTheme } from "./ThemeProvider";
import { MoonIcon, SunIcon, ChevronLeftIcon } from '@heroicons/react/24/outline';

// Hooks
import { useNotes } from '../hooks/useNotes';
import { useTags } from '../hooks/useTags';

// Lib
import { deleteAccount } from '../lib/db';
import { supabase } from '../lib/supabase';
import { formatDate } from '../lib/utils';
import logo from '../assets/logo.svg';

const NoteApp = ({ user }) => {
  const { theme, setTheme } = useTheme();

  // Custom Hooks
  const { notes, isLoading, db, addNote, editNote, removeNote } = useNotes(user);
  const { allTags, getSuggestions } = useTags(notes);

  // Local State
  const [currentNote, setCurrentNote] = useState('');
  const [showSaveButton, setShowSaveButton] = useState(false);
  const [selectedTag, setSelectedTag] = useState(null);
  const [editingNote, setEditingNote] = useState(null);
  const [tagSuggestions, setTagSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const editableRef = useRef(null);
  const [cursorPosition, setCursorPosition] = useState({ x: 0, y: 0 });
  const [showSidebar, setShowSidebar] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [isMobile, setIsMobile] = useState(false);
  const [expandedMobile, setExpandedMobile] = useState(false);

  // Auto-focus on mount & Mobile check
  useEffect(() => {
    if (editableRef.current) {
      editableRef.current.focus();
    }
    setIsMobile(window.innerWidth < 640);
  }, []);

  // Debounced save button logic
  const debouncedShowSaveButton = useCallback(
    debounce(() => {
      setShowSaveButton(true);
    }, 1000),
    [setShowSaveButton]
  );

  useEffect(() => {
    return () => {
      debouncedShowSaveButton.cancel();
    };
  }, [debouncedShowSaveButton]);

  // Resize listener
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 640);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Helper function to get the current word being typed
  const getCurrentWord = (node, offset) => {
    if (node.nodeType !== Node.TEXT_NODE) return '';

    const text = node.textContent;
    let start = offset;
    let end = offset;

    // Find word boundaries
    while (start > 0 && !/\s/.test(text[start - 1])) {
      start--;
    }
    while (end < text.length && !/\s/.test(text[end])) {
      end++;
    }

    return text.slice(start, end);
  };

  // Handle input changes
  const handleKeyDown = (e) => {
    const text = e.target.innerText;
    setCurrentNote(text);

    // Save on Ctrl+Enter
    if (e.nativeEvent.inputType === 'insertParagraph' && (e.ctrlKey || e.metaKey)) {
      e.preventDefault();
      saveNoteToDb();
      return;
    }

    if (!text.trim()) {
      setShowSaveButton(false);
    } else {
      setShowSaveButton(true);
    }

    // Get cursor position from selection
    const selection = window.getSelection();
    if (selection.rangeCount > 0) {
      const range = selection.getRangeAt(0);

      if (range.getClientRects().length > 0) {
        const rect = range.getClientRects()[0];
        setCursorPosition({
          x: rect.left,
          y: rect.top
        });
      }

      // Find the word being typed
      const currentWord = getCurrentWord(range.startContainer, range.startOffset);

      if (currentWord.startsWith('#')) {
        const partialTag = currentWord.slice(1).toLowerCase();

        if (partialTag) {
          const suggestions = getSuggestions(partialTag);
          setTagSuggestions(suggestions);
          setShowSuggestions(suggestions.length > 0);
        } else {
          setShowSuggestions(false);
        }
      } else {
        setShowSuggestions(false);
      }
    }
  };

  const handleSignOut = async () => {
    try {
      if (db) {
        const tx = db.transaction('notes', 'readwrite');
        const store = tx.objectStore('notes');
        await store.clear();
        await tx.done;
      }

      if (user.isGuest) {
        localStorage.removeItem('guestUser');
      } else {
        const { error } = await supabase.auth.signOut();
        if (error) throw error;
        await supabase.auth.clearSession();
      }

      localStorage.removeItem(`defaultNotes-${user.id}`);
      localStorage.removeItem('sb-yzgyhdrughpwaqgcgqeu-auth-token');

    } catch (error) {
      console.error('Error signing out:', error);
    } finally {
      if (typeof window !== 'undefined') {
        window.sessionStorage.clear();
        window.localStorage.clear();
      }
      window.location.href = '/';
    }
  };

  const handleDeleteNote = useCallback(async (noteId) => {
    try {
      await removeNote(noteId);
    } catch (error) {
      alert('Failed to delete note. Please try again.');
    }
  }, [removeNote]);

  const handleEditNote = (note) => {
    setEditingNote(note);
    setCurrentNote(note.content);
    setShowSaveButton(true);
    if (editableRef.current) {
      editableRef.current.innerText = note.content;
      editableRef.current.focus();
    }
  };

  const saveNoteToDb = async () => {
    try {
      if (!currentNote.trim()) return;

      if (editingNote) {
        await editNote(editingNote, currentNote);
        setEditingNote(null);
      } else {
        await addNote(currentNote);
      }

      setCurrentNote('');
      setShowSaveButton(false);

      if (editableRef.current) {
        editableRef.current.innerText = '';
        editableRef.current.focus();
      }
    } catch (error) {
      console.error('Error saving note:', error);
      alert('Failed to save note. Please try again.');
    }
  };

  const handleTagClick = useCallback((tag) => {
    if (selectedTag === tag) {
      setSelectedTag(null);
    } else {
      setSelectedTag(tag);
    }
  }, [selectedTag]);

  const handleTagSelect = (tag) => {
    const selection = window.getSelection();
    if (selection.rangeCount === 0) return;

    const range = selection.getRangeAt(0);
    const textNode = range.startContainer;

    if (textNode.nodeType === Node.TEXT_NODE) {
      const text = textNode.textContent;
      const offset = range.startOffset;

      let start = offset;
      while (start > 0 && text[start - 1] !== '#') {
        start--;
      }
      let end = offset;
      while (end < text.length && !/\s/.test(text[end])) {
        end++;
      }

      const beforeTag = text.substring(0, Math.max(0, start - 1));
      const afterTag = text.substring(end);
      const newText = `${beforeTag}#${tag} ${afterTag}`;

      textNode.textContent = newText;

      const newPosition = Math.min(
        start + tag.length + 2,
        newText.length
      );

      const newRange = document.createRange();
      newRange.setStart(textNode, newPosition);
      newRange.collapse(true);
      selection.removeAllRanges();
      selection.addRange(newRange);

      setCurrentNote(editableRef.current.innerText);
    }

    setShowSuggestions(false);
  };

  const handleDeleteAccount = async () => {
    try {
      await deleteAccount(db, user.id);
      window.location.replace('/');
    } catch (error) {
      console.error('Error deleting account:', error);
      alert('Failed to delete account. Please try again.');
    }
  };

  const filteredNotes = useMemo(() => {
    return notes.filter(note => {
      const matchesSearch = note.content.toLowerCase().includes(searchQuery.toLowerCase());
      const matchesTag = !selectedTag || note.content.includes(`#${selectedTag}`);
      return matchesSearch && matchesTag;
    });
  }, [notes, searchQuery, selectedTag]);

  return (
    <div className="h-screen bg-background overflow-hidden">
      {/* Header */}
      <header className={`
        border-b bg-background 
        transition-[padding] duration-500 cubic-bezier(0.34, 1.56, 0.64, 1)
        ${showSidebar ? 'sm:pr-[400px]' : 'sm:pr-0'}
      `}>
        <div className="flex items-center justify-between px-4 h-16">
          {/* Logo */}
          <div className="flex items-center gap-2">
            <img
              src={logo.src}
              alt="Notes"
              className="w-28 h-6 [filter:invert(0)_sepia(0)_saturate(1)_hue-rotate(0deg)_brightness(0.96)] dark:[filter:invert(1)_sepia(0)_saturate(1)_hue-rotate(0deg)_brightness(1)] text-accent-foreground"
            />
          </div>

          {/* Theme and User Menu */}
          <div className="relative">
            <div className={`
              flex items-center gap-2
              transition-all duration-500 cubic-bezier(0.34, 1.56, 0.64, 1)
              ${showSidebar ? 'translate-x-[calc(100%-6.5rem)]' : 'translate-x-0'}
              mr-4 sm:mr-0
            `}
              style={{ transform: showSidebar ? 'translateX(calc(100% - 5rem))' : 'translateX(0)' }}>
              <Button
                variant="ghost"
                size="icon"
                onClick={() => setTheme(theme === "light" ? "dark" : "light")}
              >
                {theme === "light" ? <MoonIcon className="h-4 w-4" /> : <SunIcon className="h-4 w-4" />}
              </Button>

              <UserDropdown
                user={user}
                onSignOut={handleSignOut}
                onDeleteAccount={handleDeleteAccount}
              />

              <Button
                variant="outline"
                onClick={() => setShowSidebar(true)}
                className={`
                  hidden sm:flex items-center gap-2 pl-1 pr-2 ml-1
                  transition-all duration-500 cubic-bezier(0.34, 1.56, 0.64, 1)
                  ${showSidebar
                    ? 'translate-x-[200%] opacity-0'
                    : 'translate-x-0 opacity-100'
                  }
                `}
              >
                <ChevronLeftIcon className="h-4 w-4" />
                <span>Read</span>
              </Button>
            </div>
          </div>
        </div>
      </header>

      <div className={`
        flex flex-col sm:flex-row sm:h-[calc(100vh-128px)]
        transition-[padding] duration-500 cubic-bezier(0.34, 1.56, 0.64, 1)
        ${showSidebar ? 'sm:pr-[400px]' : 'sm:pr-0'}
      `}>
        {/* Editor Area */}
        <Editor
          editableRef={editableRef}
          onInput={handleKeyDown}
          showSaveButton={showSaveButton}
          onSave={saveNoteToDb}
          editingNote={editingNote}
          showSuggestions={showSuggestions}
          tagSuggestions={tagSuggestions}
          cursorPosition={cursorPosition}
          onTagSelect={handleTagSelect}
        />

        {/* Sidebar */}
        <Sidebar
          showSidebar={showSidebar}
          setShowSidebar={setShowSidebar}
          isMobile={isMobile}
          expandedMobile={expandedMobile}
          setExpandedMobile={setExpandedMobile}
          notesCount={notes.length}
          tags={allTags}
          selectedTag={selectedTag}
          onTagClick={handleTagClick}
          filteredNotes={filteredNotes}
          searchQuery={searchQuery}
          setSearchQuery={setSearchQuery}
          onEdit={handleEditNote}
          onDelete={handleDeleteNote}
        />
      </div>

      {/* Footer */}
      <div className={`
        hidden sm:flex bg-background
        transition-[padding] duration-500 cubic-bezier(0.34, 1.56, 0.64, 1)
        ${showSidebar ? 'sm:pr-[400px]' : 'sm:pr-0'}
      `}>
        <div className="w-full max-w-[1600px] mx-auto px-8 py-4 flex items-center justify-center">
          <div className="flex items-center gap-2">
            <span className="text-muted-foreground">Designed and developed</span>
            <a
              href="https://www.shakeb.in"
              target="_blank"
              rel="noopener noreferrer"
              className="font-medium hover:text-primary transition-colors"
            >
              Shakeb
            </a>
          </div>
        </div>
      </div>

      {/* Mobile Bottom Sheet */}
      <div className="sm:hidden">
        <BottomSheet
          notes={notes}
          onDeleteNote={handleDeleteNote}
          onEditNote={handleEditNote}
          formatDate={formatDate}
          selectedTag={selectedTag}
          setSelectedTag={setSelectedTag}
          expandedMobile={expandedMobile}
          setExpandedMobile={setExpandedMobile}
          searchQuery={searchQuery}
          setSearchQuery={setSearchQuery}
        />
      </div>
    </div>
  );
};

if (typeof window !== 'undefined') {
  window.selectTag = (tag) => {
    const tagName = tag.slice(1);
    const event = new CustomEvent('selectTag', { detail: tagName });
    window.dispatchEvent(event);
  };
}

export default NoteApp;